import React from "react";
import { useEffect } from "react";
import { Link } from "react-router-dom";
import ModalImage from "react-modal-image";
import { useDispatch, useSelector } from "react-redux";
import { addToCart, removeCart } from "../actions/cartAction";

import { BsCart4 } from "react-icons/bs";
import SEO from "../components/SEO";

const Cart = ({ match, location, history }) => {
  const { cartItems } = useSelector((state) => state.cart);

  const user = useSelector((state) => state.user);
  const qty = location.search ? Number(location.search.split("=")[1]) : 1;

  const dispatch = useDispatch();

  const removeFromCartHandler = (id) => {
    dispatch(removeCart(id));
  };

  const checkOutHandler = () => {
    if (!user) {
      history.push("/login");
    } else {
      history.push("/shipping");
    }
  };

  if (!user) {
    return (
      <div className="container text-center">
        <div className="my-5">
          <p className=" text-secondary"> You are not login </p>
          <Link to="/login">Login</Link>
        </div>
      </div>
    );
  }

  return (
    <>
    <SEO title={"Cart"} />
    <section>
      <div className="max-w-screen-xl px-4 py-8 mx-auto sm:px-6 sm:py-12 lg:px-8">
        <div className="max-w-3xl mx-auto">
          <header className="text-center">
            <h1 className="text-xl font-bold text-gray-700 sm:text-3xl ">
              Your Cart
            </h1>
          </header>

          <div className="mt-8">
            <ul className="space-y-4">
              {user &&
              cartItems.filter((x) => x.userId === user._id).length === 0 ? (
                <>
                  {" "}
                  <p>Your cart is empty</p> <Link to="/">Go back </Link>{" "}
                </>
              ) : (
                <>
                  {cartItems
                    .filter((x) => x.userId === user._id)
                    .map((x) => (
                      <li className="flex items-center">
                        {x.images.length > 0 && (
                          <img
                            src={x.images[0].url}
                            //   src="https://images.unsplash.com/photo-1618354691373-d851c5c3a990?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=830&q=80"
                            alt=""
                            className="object-cover w-16 h-16 rounded"
                          />
                        )}

                        <div className="ml-4">
                          <h3 className="text-sm text-gray-900">
                            Basic Tee 6-Pack
                            <Link to={`/product/${x.product}`}> {x.name} </Link>
                          </h3>

                          <dl className="mt-0.5 space-y-px text-[10px] text-gray-600">
                            <div>
                              <dt className="inline">Price: </dt>
                              <dd className="inline"> {x.price} PKR </dd>
                            </div>
                          </dl>
                        </div>

                        <div className="flex items-center justify-end flex-1 gap-2">
                          <form>
                            <label for="Line1Qty" className="sr-only">
                              {" "}
                              Quantity{" "}
                            </label>

                            <input
                              defaultValue={x.qty}
                              onChange={(e) =>
                                dispatch(
                                  addToCart(
                                    x.product,
                                    Number(e.target.value),
                                    user._id
                                  )
                                )
                              }
                              type="number"
                              min="1"
                              value="1"
                              id="Line1Qty"
                              className="h-8 w-12 rounded border-gray-200 bg-gray-50 p-0 text-center text-xs text-gray-600 [-moz-appearance:_textfield] focus:outline-none [&::-webkit-outer-spin-button]:m-0 [&::-webkit-outer-spin-button]:appearance-none [&::-webkit-inner-spin-button]:m-0 [&::-webkit-inner-spin-button]:appearance-none"
                            />
                          </form>

                          <button
                            className="text-gray-600 transition hover:text-red-600"
                            onClick={() => removeFromCartHandler(x.product)}
                          >
                            <span className="sr-only">Remove item</span>

                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              fill="none"
                              viewBox="0 0 24 24"
                              stroke-width="1.5"
                              stroke="currentColor"
                              className="w-4 h-4"
                            >
                              <path
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0"
                              />
                            </svg>
                          </button>
                        </div>
                      </li>
                    ))}
                </>
              )}
            </ul>

            <div className="flex justify-end pt-8 mt-8 border-t border-gray-100">
              <div className="w-screen max-w-lg space-y-4">
                <dl className="space-y-0.5 text-sm text-gray-700">
                  <div className="flex justify-between">
                    <dt>
                      Subtotal (
                      {cartItems
                        .filter((x) => x.userId === user._id)
                        .reduce((acc, item) => acc + item.qty, 0)}
                      ) items
                    </dt>
                    <dd>
                      {cartItems
                        .filter((x) => x.userId === user._id)
                        .reduce((acc, item) => acc + item.qty * item.price, 0)
                        .toFixed(2)}{" "}
                      PKR
                    </dd>
                  </div>
                </dl>

                <div className="flex justify-end">
                  <button
                    disabled={
                      cartItems.filter((x) => x.userId === user._id).length ===
                      0
                    }
                    onClick={checkOutHandler}
                    className="block px-5 py-3 text-sm text-gray-100 transition bg-gray-700 rounded hover:bg-gray-600"
                  >
                    Processed to check out
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    </>
  );
};

export default Cart;
